.is-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(50%);
}

.visually-hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity var(--transition-duration) var(--timing-function);
}
